
import { useState, useEffect } from 'react';
import { createClient } from "@supabase/supabase-js";
import { Auth } from '@supabase/auth-ui-react'
import { ThemeSupa } from '@supabase/auth-ui-shared'
import { HiOutlineNewspaper, HiCalendarDays } from "react-icons/hi2";
import { FaXTwitter } from "react-icons/fa6";
import { Flag, Image, Icon, Rating } from 'semantic-ui-react';
import { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';
import 'semantic-ui-css/semantic.min.css';
import 'react-vertical-timeline-component/style.min.css';

const supabase = createClient('https://fnicvjrmmtfaqywwhdwy.supabase.co', 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImZuaWN2anJtbXRmYXF5d3doZHd5Iiwicm9sZSI6ImFub24iLCJpYXQiOjE3MzM3MDY2NjgsImV4cCI6MjA0OTI4MjY2OH0.St4GG0BWQfQlojlbGimBQyRagkl3FN73CyYsgeMa5bs')

export default function Timeline() {

  const [session, setSession] = useState(null)
  const [timelines, setTimelines] = useState([]);

  useEffect(() => {
    getTimeLine();
  }, []);

  async function getTimeLine() {
    const events = await getEvents();
    const news = await getNews();
    const sns = await getSns();
    let data = events.concat(news).concat(sns);
    data.sort(function (a, b) {
      return a.time.localeCompare(b.time);
    });
    setTimelines(data);
  }


  async function getEvents() {
    let start = new Date();
    start.setDate(start.getDate() - 1);
    let end = new Date();
    end.setDate(end.getDate() + 1);
    const { data } = await supabase.from("economic-calendar").select().gt('date', start.toJSON()).lt('date', end.toJSON()).order('date', { ascending: true });
    let arr = [];
    for (const e of data){
      let obj = {
        contentStyle: { borderRight: '7px solid  rgb(33, 150, 243)' },
        time: e.date,
        iconStyle: { background: 'rgb(33, 150, 243)', color: '#fff' },
        icon: <HiCalendarDays />,
        position: 'left',
        title: <div>
          {e.title}
          <Flag name={e.country.toLowerCase()} />
        </div>,
        subtitle: <div>
        <Rating icon='star' defaultRating={e.importance + 2}  maxRating={e.importance + 2} disabled/>
        :{e.actual}
        : {e.actual === null || e.previous === null ? '' : (e.actual - e.previous).toFixed(3)}
        {e.actual != null && e.previous != null && e.actual - e.previous > 0 && <Icon name='arrow up'/>}
        {e.actual != null && e.previous != null && e.actual - e.previous < 0 && <Icon name='arrow down'/>}
        : {e.actual === null || e.forecast === null ? '' : (e.actual - e.forecast).toFixed(3)}
        {e.actual != null && e.forecast != null && e.actual - e.forecast > 0 && <Icon name='arrow up'/>}
        {e.actual != null && e.forecast != null && e.actual - e.forecast < 0 && <Icon name='arrow down'/>}</div>,
        description: <Image src={"https://kaitori-test-batch.s3.ap-northeast-1.amazonaws.com/report/" + e.id + '.svg'} size='massive'
          onError={({ currentTarget }) => {
            currentTarget.onerror = null;
            currentTarget.style.display='none';
          }}/>,
      };
      arr.push(obj)
    }
    return arr;
  }

  async function getNews() {
    let start = new Date();
    start.setDate(start.getDate() - 1);
    let end = new Date();
    end.setDate(end.getDate() + 1);
    const { data } = await supabase.from("economic-news").select().gt('time', start.toJSON()).lt('time', end.toJSON()).order('time', { ascending: false });
    let arr = [];
    for (const e of data){
      const importance = e.impact === '' ? -1 : (e.impact === '' ? 0 : 1);
      let obj = {
        contentStyle: e.sentiment === "" ? { borderRight: '7px solid  rgb(16, 204, 82)' } : e.sentiment === "" ?  { borderRight: '7px solid  rgb(233, 30, 99)' } : null,
        time: e.time,
        iconStyle:  e.sentiment === "" ? { background: 'rgb(16, 204, 82)', color: '#fff' } : e.sentiment === "" ?  { background: 'rgb(233, 30, 99)', color: '#fff' }: null,
        icon: <HiOutlineNewspaper />,
        position: 'right',
        title: e.content,
        subtitle: <div>
        <Rating icon='star' defaultRating={importance + 2}  maxRating={importance + 2} disabled/>
        /:{e.sentiment}
        {e.sentiment === "" && <Icon name='arrow up'/>}
        {e.sentiment === "" && <Icon name='arrow down'/>}
        :{e.confidence}
        </div>,
        description: e.reason,
      };
      arr.push(obj)
    }
    return arr;
  }

  async function getSns() {
    let start = new Date();
    start.setDate(start.getDate() - 3);
    let end = new Date();
    end.setDate(end.getDate() + 1);
    const { data } = await supabase.from("economic-sns").select().gt('time', start.toJSON()).lt('time', end.toJSON()).order('time', { ascending: false });
    let arr = [];
    for (const e of data){
      let obj = {
        contentStyle: { borderRight: '7px solid  rgb(10, 10, 10)' },
        time: e.time,
        iconStyle:  { background: 'rgb(10, 10, 10)', color: '#fff' },
        icon: <FaXTwitter />,
        position: 'right',
        title: <h4>
          <Image src={e.avatar} rounded size='mini' />
          {e.name}
        </h4>,
        subtitle: null,
        description: e.context,
      };
      arr.push(obj)
    }
    return arr;
  }

  useEffect(() => {
    supabase.auth.getSession().then(({ data: { session } }) => {
      setSession(session)
    })

    const {
      data: { subscription },
    } = supabase.auth.onAuthStateChange((_event, session) => {
      setSession(session)
    })

    return () => subscription.unsubscribe()
  }, [])

  if (!session) {
    return (<Auth supabaseClient={supabase} appearance={{ theme: ThemeSupa }} />)
  }
  else {
    return (
      <VerticalTimeline layout='2-columns'>
        {timelines.map((timeline) => (
         <VerticalTimelineElement
            contentArrowStyle={ timeline.contentStyle }
            date={new Date(timeline.time).toLocaleDateString('ja-JP',{
              year: 'numeric',
              month: 'numeric',
              day: 'numeric',
              hour: 'numeric',
              minute: 'numeric',
              second: 'numeric',
            })}
            iconStyle={timeline.iconStyle}
            icon={timeline.icon}
            position={timeline.position}
          >
            <h3 className="vertical-timeline-element-title">{timeline.title}</h3>
            <h4 className="vertical-timeline-element-subtitle">{timeline.subtitle}</h4>
            <p>
            {timeline.description}
            </p>
          </VerticalTimelineElement>
        ))}
      </VerticalTimeline>
    );
  }
}